<template>
  <div class=" text-center loginscreen  animated fadeInDown modify">
    <div class="row mt">
      <h1>修改{{ name }}密码</h1>

      <form class="form-horizontal middle-box" @submit.prevent="editBasic" v-if="!id">
        <div class="form-group">
          <label class="control-label col-sm-3"><span class="MustFill">*</span>真实姓名</label>
          <div class="col-sm-9">
            <input type="text" class="form-control" name="name" v-model="basic.name">
          </div>
        </div>

        <div class="form-group">
          <label class="control-label col-sm-3"><span class="MustFill">*</span>联系电话</label>
          <div class="col-sm-9">
            <input type="text" maxlength="11" class="form-control" name="phone" v-model="basic.phone" onkeyup="this.value=this.value.replace(/[^0-9a-zA-Z]/g,'')">
          </div>
        </div>

        <div class="form-group">
          <label class="control-label col-sm-3"><span class="MustFill">*</span>邮箱</label>
          <div class="col-sm-9">
            <input type="email" class="form-control" name="email" v-model="basic.email">
          </div>
        </div>

        <div class="form-group">
          <div class="col-sm-offset-3 col-sm-9">
            <button type="submit" class="btn btn-primary block full-width">确定</button>
          </div>
        </div>
        <hr>
      </form>

      <form class="form-horizontal middle-box" @submit.prevent="confirm">

        <template v-if="!id">

          <div class="form-group" >
            <label class="control-label col-sm-3">旧密码</label>
            <div class="col-sm-9">
              <input type="password" class="form-control" v-model="form.old_password" onkeyup="this.value=this.value.replace(/[^0-9a-zA-Z]/g,'')">
            </div>
          </div>

        </template>

        <div class="form-group">
          <label class="control-label col-sm-3">修改密码</label>
          <div class="col-sm-9">
            <input type="password" class="form-control" v-model="form.new_password" onkeyup="this.value=this.value.replace(/[^0-9a-zA-Z]/g,'')">
          </div>
        </div>
        <div class="form-group">
          <label class="control-label col-sm-3">确认密码</label>
          <div class="col-sm-9">
            <input type="password" class="form-control" v-model="new_password" onkeyup="this.value=this.value.replace(/[^0-9a-zA-Z]/g,'')">
          </div>
        </div>

        <div class="form-group">
          <div class="col-sm-offset-3 col-sm-9">
            <button type="submit" class="btn btn-primary block full-width">确定</button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import Cache from '@/assets/cache'
import Service from '@/assets/service/service'
export default {
  name: 'modifyPassword',
  data () {
    return {
      id: this.$route.params['id'],
      name: this.$route.query['name'],
      form: {},
      basic: {},
      new_password: null,
      user: Cache.getUser()
    }
  },
  created () {
    this.basic['email'] = this.user['email']
    this.basic['phone'] = this.user['phone']
    this.basic['name'] = this.user['name']
  },
  methods: {
    editBasic () {
      Service.Users.edit(this.user.id, this.basic).then(response => {
        if (response.code == 200) {
          toastr.success(response.msg)
          setTimeout(() => {
            parent.location.href = '/login'
          }, 1000)
        } else {
          toastr.error(response.msg)
        }
      })
    },
    confirm () {
      if (!this.id && !this.form['old_password']) {
        toastr.warning('请输入旧密码')
        return
      }
      if (!this.form['new_password']) {
        toastr.warning('密码不能为空')
        return
      }
      if (this.form.new_password == Number(this.new_password) || this.form.new_password.length < 6) {
        toastr.warning('请输入6位以上的字母加数字')
        return
      }
      if (this.form['new_password'] != this.new_password) {
        toastr.error('两次输入的密码不同')
        return
      } else {
        const id = this.id ? this.id :this.user.id
        Service.Users.edit(id, this.form).then(response => {
          if (response.code == 200) {
            toastr.success(response.msg)
            setTimeout(() => {
              if (!this.id){
                parent.location.href = '/login'
              }
            }, 1000)
          } else {
            toastr.error(response.msg)
          }
        })
      }
    }
  }
}
</script>

<style scoped>
  .mt{
    margin: 100px 0 0;
  }
  .mt h1{
    font-weight: bold;
  }
  .modify {
    position: relative;
  }
</style>



